
import Rotas from '@/router/Rotas';
import { Vue, Component } from "vue-property-decorator";
import EmpresaService from "@/Service/EmpresaService";
import { Inject } from "typescript-ioc";
import EmpresaDto from '@/Model/Empresa/EmpresaDto';
import { ColaboradorDto } from '@/Model/ColaboradorDto';
import { EnderecoDto } from '@/Model/EnderecoDto';
import Login from '@/Model/Login';

@Component({
  components:{

  }
})
export default class EtapasCadastroEmpresa extends Vue {
  public drawer = false;
  public group = null;
  public cadastroItens = Rotas.Cadastros.CadastroItens;
  public cadastroProdutos = Rotas.Cadastros.CadastroProdutos;
  public Empresa: EmpresaDto = new EmpresaDto();
  public Colaborador = new ColaboradorDto();
  public Endereco = new EnderecoDto();
  @Inject
  public empresaService!: EmpresaService;

  public nome!: string;

  public Inicio = Rotas.Inicio;
  public CadastroItens = Rotas.Cadastros.CadastroItens;
  public Ajuda = Rotas.Ajuda;
  public RealizaOrcamento = Rotas.Cadastros.RealizaOrcamento;


  public async cadastrarEmpresa(){
  if(this.validacaoFormularaio() && this.verificaConfimacoSenha() === null ){
    this.Empresa.colaboradores.push(this.Colaborador);
    this.Empresa.enderecos.push(this.Endereco);
    await this.empresaService.cadastrarEmpresa(this.Empresa)
    .then(async ()=> 
       await this.empresaService.obterInformacoesEmpresa(new Login(this.Colaborador.email, this.Colaborador.senha))
       .then(()=> this.$router.push('/')))
    alert('tudo certo')
  }

  }
  //opo usando Rules para validar formulario
  public emailRules: ((v: string) => string | boolean)[] = [
    (v: string) => !!v || 'E-mail  obrigatrio',
    (v: string) =>
      /^(([^<>()[\]\\.,;:\s@']+(\.[^<>()\\[\]\\.,;:\s@']+)*)|('.+'))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) ||
      'E-mail invlido',
  ];

  public verificaConfimacoSenha(): string[] | null {
    return this.Colaborador.senha === this.confirmaSenha ? null :["Confirmao de senha incorreta"];
  }

  public sair(){
    localStorage.removeItem('ocirenegotnemacro');
    this.$router.push(Rotas.Visitante.Login);
  }

  get numberValidation() {
    return [(value: string) => /^\d+$/.test(value) || 'Apenas nmeros so permitidos.'];
  }

  public show1= false;

  public confirmaSenha: string = ''; 

  public getErrorMessage(fieldName: string): string[] | null {
    switch (fieldName) {
      case "nomeEmpresa": 
        return !this.Empresa.nome ? ["Campo obrigatrio"] : null;
      case "nomeGerente":
        return !this.Colaborador.nome ? ["Campo obrigatrio"] : null;
      case "telefone":
        return !this.Empresa.telefone ? ["Campo obrigatrio"] : null;
      case "cnpj":
        return !this.Empresa.cnpj ? ["Campo obrigatrio"] : null;
      case "cpf":
        return !this.Colaborador.cpf ? ["Campo obrigatrio"] : null;
       case "senha":
         return !this.Colaborador.senha ? ["Campo obrigatrio"] : null;
       case "cidade":
         return !this.Endereco.cidade ? ["Campo obrigatrio"] : null;
        case "estado":
         return !this.Endereco.estado ? ["Campo obrigatrio"] : null;
         case "bairro":
         return !this.Endereco.bairro ? ["Campo obrigatrio"] : null;
        case "rua":
         return !this.Endereco.rua ? ["Campo obrigatrio"] : null;
         case "numero":
         return !this.Endereco.numero ? ["Campo obrigatrio"] : null;
        case "cep":
         return !this.Endereco.cep ? ["Campo obrigatrio"] : null;

      default:
        return null;
    }
  }
  private validacaoFormularaio(): boolean {
    if(    !this.Empresa.nome   ||
      !this.Colaborador.nome    ||
  !this.Empresa.telefone        ||
     !this.Empresa.cnpj         ||
      !this.Colaborador.cpf     ||
        !this.Colaborador.senha ||
       !this.Endereco.cidade    ||
      !this.Endereco.estado     ||
        !this.Endereco.bairro   ||
         !this.Endereco.rua     ||
          !this.Endereco.numero ||
        !this.Endereco.cep  || 
        !/^(([^<>()[\]\\.,;:\s@']+(\.[^<>()\\[\]\\.,;:\s@']+)*)|('.+'))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.Colaborador.email)
        
        ){
          return false
        }
        return true
  }
}
